<template>
	<div>
		<base-dialog :show="!!error" title="Error Condition" @close="handleError">
			<p>{{ error }}</p>
		</base-dialog>
		<base-dialog :show="isLoading" title="Authenticating" fixed>
			<base-spinner></base-spinner>
		</base-dialog>
		<base-card>
			<form @submit.prevent="submitForm">
				<div class="form-control">
					<label for="email">E-mail</label>
					<input type="email" id="email" v-model.trim="email" />
				</div>
				<div class="form-control">
					<label for="password">Password</label>
					<input type="password" id="password" v-model.trim="password" />
				</div>
				<p v-if="!formIsValid">Please enter a valid email and password combination.</p>
				<base-button>{{ submitButtonCaption }}</base-button>
				<base-button type="button" mode="flat" @click="switchAuthMode">
					{{ switchModeButtonCaption }}
				</base-button>
			</form>
		</base-card>
	</div>
</template>

<script>
export default {
	data() {
		return {
			email: '',
			password: '',
			formIsValid: true,
			mode: 'signin',
			isLoading: false,
			error: null,
		};
	},

	methods: {
		async submitForm() {
			this.formIsValid = true;

			if (this.email === '' || !this.email.includes('@') || this.password.length < 6) {
				this.formIsValid = false;
				return;
			}

			this.isLoading = true;

			const actionPayload = {
				email: this.email,
				password: this.password,
			};

			try {
				if (this.mode === 'signin') {
					await this.$store.dispatch('signin', actionPayload);
				} else {
					await this.$store.dispatch('signup', actionPayload);
				}

				const redirectURL = '/' + (this.$route.query.redirect || 'coaches');

				this.$router.replace(redirectURL);
			} catch (err) {
				this.error = err.message || 'The authentication process failed.';
			}

			this.isLoading = false;
		},

		switchAuthMode() {
			if (this.mode === 'signin') {
				this.mode = 'signup';
			} else {
				this.mode = 'signin';
			}
		},

		handleError() {
			this.error = null;
		},
	},

	computed: {
		submitButtonCaption() {
			if (this.mode === 'signin') {
				return 'Sign In';
			} else {
				return 'Sign Up';
			}
		},

		switchModeButtonCaption() {
			if (this.mode === 'signin') {
				return 'Sign Up';
			} else {
				return 'Sign In';
			}
		},
	},
};
</script>

<style scoped>
form {
	margin: 1rem;
	padding: 1rem;
}

.form-control {
	margin: 0.5rem 0;
}

label {
	font-weight: bold;
	margin-bottom: 0.5rem;
	display: block;
}

input,
textarea {
	display: block;
	width: 100%;
	font: inherit;
	border: 1px solid #ccc;
	padding: 0.15rem;
}

input:focus,
textarea:focus {
	border-color: #3d008d;
	background-color: #faf6ff;
	outline: none;
}
</style>
